@Component({
  selector: 'app-tab1',
  templateUrl: 'tab1.page.html',
  styleUrls: ['tab1.page.scss']
})
export class Tab1Page {
  searchText: string = '';
  cases: any = [];
  isLoading: boolean = false;
  allCaseCount: number = 0;
  activeCaseCount: number = 0;
  pendingCaseCount: number = 0;
  closedCaseCount: number = 0;
  totalActiveCases: number = 0;

  constructor(
    public actionSheetController: ActionSheetController,
    private router: Router,
    private route: ActivatedRoute,
    ) {}
    
  ngOnInit() {
    localStorage.setItem("cases", JSON.stringify(theCases));
    console.log('wow');
  }

  ionViewWillEnter() {
    this.isLoading = true;
  }

  ionViewDidEnter() {
    const timeout = setTimeout(()=> {
      this.isLoading = false;
      this.cases = JSON.parse(localStorage.getItem("cases"));
      this.allCaseCount = this.cases.length;
      this.activeCaseCount = this.cases.filter(item => item.status === 'Active').length;
      this.pendingCaseCount = this.cases.filter(item => item.status === 'Pending').length;
      this.closedCaseCount = this.cases.filter(item => item.status === 'Closed').length;
      this.totalActiveCases = this.activeCaseCount + this.pendingCaseCount;
      console.log(this.allCaseCount, this.activeCaseCount, this.pendingCaseCount, this.closedCaseCount, 'this.allCaseCount, this.activeCaseCount, this.pendingCaseCount, this.closedCaseCount');

      this.sortCases();

      // console.log("Looks like I'm about to leave :(");
      clearTimeout(timeout);
    }, 200);

  }

  sortCases() {
    const unreadActive = this.cases.filter(item => item.status === 'Active' && !item.read);
    const readActive = this.cases.filter(item => item.status === 'Active' && item.read);
    const pendingForwarded = this.cases.filter(item => item.status === 'Pending' && item.callStatus === 'Forwarded');
    const pendingAccepted = this.cases.filter(item => item.status === 'Pending' && item.callStatus === 'Accepted');
    const closed = this.cases.filter(item => item.status === 'Closed');
    this.cases = [...unreadActive, ...readActive, ...pendingForwarded, ...pendingAccepted, ...closed];
    localStorage.setItem("cases", JSON.stringify(this.cases));
  }

  async presentActionSheet() {

    const actionSheet = await this.actionSheetController.create({
      header: 'Filter',
      buttons: [{
        text: `All Cases (${this.allCaseCount})`,
        handler: () => {
          console.log('All cases clicked');
          this.router.navigate(['/tabs/tab1'], { replaceUrl: true });
        }
      }, {
        text: `Active Cases (${this.activeCaseCount})`,
        handler: () => {
          console.log('Active cases clicked');
          this.router.navigate(['/tabs/tab1/case/active'], { replaceUrl: true });

        }
      }, {
        text: `Pending Cases (${this.pendingCaseCount})`,
        handler: () => {
          console.log('Pending clicked');
          this.router.navigate(['/tabs/tab1/case/pending'], { replaceUrl: true });
        }
      }, {
        text: `Closed Cases (${this.closedCaseCount})`,
        handler: () => {
          console.log('Closed Cases clicked');
          this.router.navigate(['/tabs/tab1/case/closed'], { replaceUrl: true });
        }
      }, {
        text: 'Cancel',
        role: 'cancel',
        handler: () => {
          console.log('Cancel clicked');
        }
      }]
    });
    await actionSheet.present();
  }

}
